import request from '@/utils/request'

let PortApi = {
    /* 体验者列表 */
    versionList(data,errorback){
        return request._post('/admin/port/versionList', data, errorback);
    },
    /* 体验者列表 */
    syncCodeTemplate(data,errorback){
        return request._post('/admin/port/codeTemplateList', data, errorback);
    }, 
}

export default PortApi;



